import { Component, ReactNode } from "react";
import style from './index.less'

class About extends Component {
    render(): ReactNode {
        return (<div>
            <div className={style.title}></div>
            <div className={style.content}>MIT</div>
            <div className={style.content}><a target="_blank" href="https://github.com/239573049">token</a></div>
        </div>)
    }
}

export default About